.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#lin{
  filter: grayscale(100%);
}
.card{
  border: none;
}
#lin:hover{
filter: grayscale(0%);
}
#bannertxt{
  animation: ltor 1.5s;
}
@keyframes ltor {
  from {
    margin-right: 30%;
    opacity: 0;
  }
  to {

    margin-right: 0%;
    opacity: 1;
  }
}